.canvas{
    min-height: 100%;
    background-color: #fff;
    overflow: hidden;
}

.component-wrapper{
    margin:12px;
    border:1px solid #fff;
    padding:12px;
    border-radius: 3px;
    &:hover{
        border-color: #d9d9d9;
    }
}

.selected{
    border:#1890ff solid 1px !important;
}

.locked{
    opacity: .5;
    cursor: not-allowed;
}

.component{
    pointer-events: none;
}